import {
  JSONType,
  HttpMethod,
  TrackParametersType,
  IExternalRequestDispatcher,
  KameleoonError,
  KameleoonException,
} from '@kameleoon/javascript-sdk-core';
import { DELAY_MILLISECONDS, NUMBER_OF_RETRIES } from './constants';

export class RequestDispatcher implements IExternalRequestDispatcher {
  public async track({
    url,
    headers,
    body,
  }: TrackParametersType): Promise<boolean> {
    const response = await fetch(url, {
      method: HttpMethod.Post,
      headers,
      body,
    });

    return response.ok;
  }

  public async getClientConfiguration(url: string): Promise<JSONType> {
    for (let i = 1; i <= NUMBER_OF_RETRIES; i++) {
      try {
        const response = await fetch(url, {
          method: HttpMethod.Get,
        });

        if (response.ok) {
          return response.json();
        }

        if (response.status !== 504) {
          throw new KameleoonError(KameleoonException.ClientConfiguration);
        }
      } catch (err) {
        throw new KameleoonError(KameleoonException.ClientConfiguration);
      }

      if (i < NUMBER_OF_RETRIES) {
        await new Promise((resolve) => setTimeout(resolve, DELAY_MILLISECONDS));
      }
    }

    throw new KameleoonError(KameleoonException.MaximumRetriesReached);
  }

  public async getRemoteData(url: string): Promise<JSONType> {
    const response = await fetch(url, {
      method: HttpMethod.Get,
    });

    if (response.ok) {
      return response.json();
    }

    throw new KameleoonError(KameleoonException.RemoteData);
  }
}
